<template>
  <v-container>
    <h2>Timeout Snackbar</h2>
    <div class="text-center">
      <v-btn
        dark
        color="orange darken-2"
        @click="snackbar = true"
      >
        Open Snackbar
      </v-btn>

      <v-snackbar
        v-model="snackbar"
        :timeout="timeout"
      >
        {{ text }}

        <template v-slot:action="{ attrs }">
          <v-btn
            color="blue"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    snackbar: false,
    text: 'My timeout is set to 2000.',
    timeout: 2000,
  }),
};
</script>
